<template>
  <div class="collect-page">
    <van-nav-bar title="我的收藏"/>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多数据了" @load="onLoad">
      <ArticleItem v-for="item in list" :key="item.id" :item="item"></ArticleItem>
    </van-list>
  </div>
</template>

<script>
import { getArticlesCollect } from '@/api/article'
export default {
  name: 'CollectPage',
  data () {
    return {
      list: [],
      page: 1,
      loading: false,
      finished: false
    }
  },
  // async created () {
  //   const { data } = await getArticlesCollect({ page: this.page })
  //   this.list = data.rows
  // },
  methods: {
    async onLoad () {
      const { data } = await getArticlesCollect({ page: this.page })
      console.log(data)
      this.list.push(...data.rows)
      this.page++
      this.loading = false

      if (this.page > data.pageTotal) {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .collect-page {
    margin-bottom: 20px;
    margin-top: 14px;
  }
</style>
